html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #FFFFFF;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-color: #2c2c2c;
}
.header__barra {
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}

.navegacion {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    margin-top: 0;
  }
}
.navegacion__link {
  color: #FFFFFF;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: 700;
  text-transform: capitalize;
  padding: 1rem;
  margin: 0;
  transition-property: background-color;
  transition-duration: 300ms;
  transition-timing-function: ease;
}
.navegacion__link:hover {
  background-color: #FFFFFF;
  opacity: 0.7;
  color: #000000;
}
@media (min-width: 768px) {
  .navegacion__link {
    display: unset;
    padding: 1.5rem;
  }
}

.slider {
  overflow: hidden;
  position: relative;
}
.slider__slide {
  height: 100%;
  flex-shrink: 0;
  width: 100%;
  position: relative;
  transition-property: transform;
}
.slider__imagen1, .slider__imagen2, .slider__imagen3 {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 30rem;
}
@media (min-width: 768px) {
  .slider__imagen1, .slider__imagen2, .slider__imagen3 {
    min-height: 45rem;
  }
}
.slider__imagen1 {
  background-image: url(../img/header.svg);
}
.slider__imagen2 {
  background-image: url(../img/header.svg);
}
.slider__imagen3 {
  background-image: url(../img/header.svg);
}
.slider__contenido {
  max-width: 50rem;
  margin: auto;
  transform: translateY(3rem);
  text-align: center;
}
@media (min-width: 768px) {
  .slider__contenido {
    min-width: 75rem;
    margin: auto;
    transform: translateY(15rem);
    text-align: center;
  }
}
.slider__heading {
  color: #FFFFFF;
  text-transform: capitalize;
  font-size: 5rem;
  margin: 0;
}
.slider__texto {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin: 0;
}

.descripcion {
  height: 25rem;
  text-align: center;
  max-width: 75rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contenido__parrafo {
  font-size: 2.4rem;
  font-style: normal;
  color: #0c0d0e;
}
.contenido__heading {
  text-transform: capitalize;
  font-size: 5.4rem;
  font-style: normal;
  color: #000;
}

.typed-cursor- {
  display: none;
}

