@media (min-width: 768px) {
  .nosotros__grid {
    display: grid;
    gap: 0rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.nosotro {
  text-align: center;
  padding: 8.5rem 5rem;
}
.nosotro--mision, .nosotro--vision {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.nosotro--mision {
  background-image: url(../img/mision-nosotro.jpg);
}
.nosotro--vision {
  background-image: url(../img/vision-nosotro.jpg);
}
.nosotro__heading {
  color: #FFFFFF;
  text-transform: capitalize;
  font-size: 4rem;
}
.nosotro__texto {
  color: #FFFFFF;
  font-size: 1.8rem;
}

.mt-5 {
  margin-top: 5rem;
}

.proceso {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  background-repeat: no-repeat;
  background-position: bottom center;
  margin-bottom: 5rem;
}
.proceso__heading {
  font-size: 5rem;
  text-align: center;
  text-transform: capitalize;
}

.proceso:last-of-type {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .proceso {
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-proceso {
  padding: 5rem;
  background-color: #FFFFFF;
}

.basico {
  background-image: url(../img/header.svg);
  background-size: cover;
}

@media (min-width: 768px) {
  .basico {
    background-position: top right;
  }

  .basico .contenido-proceso {
    grid-row: 2/3;
    padding: 2rem 2rem 2rem 0;
  }
}
.premier {
  background-image: url(../img/header.svg);
  background-size: cover;
}

@media (min-width: 768px) {
  .premier {
    background-position: top left;
  }

  .premier .contenido-proceso {
    grid-column: 2/3;
    padding: 2rem;
  }
}
.elite {
  background-image: url(../img/header.svg);
  background-size: cover;
}

@media (min-width: 768px) {
  .elite {
    background-position: top right;
  }

  .elite .contenido-proceso {
    padding: 2rem 2rem 2rem 0;
  }
}
/* Boton */
.btn {
  color: #000;
  background-color: #FFFFFF;
  border: 1px solid #000;
  padding: 0.5rem 2rem;
  transition: background-color 0.3s;
}

.btn:hover {
  background-color: #0c0d0e;
  color: #FFFFFF;
}

/** Galeria */
.galeria {
  min-height: 75rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}
.galeria__heading {
  font-size: 5rem;
  text-transform: capitalize;
  text-align: center;
}

@media (min-width: 768px) {
  .galeria {
    grid-template-columns: repeat(3, 1fr);
  }
}
.galeria img {
  object-fit: cover;
  height: 100%;
}

.galeria .imagen:nth-child(2) {
  grid-column: 2/3;
  grid-row: 1/3;
}

@media (min-width: 768px) {
  .galeria .imagen:nth-child(2) {
    grid-column: 1/2;
    grid-row: 2/4;
  }
}
.galeria .imagen:nth-child(3) {
  grid-row: 3/5;
  grid-column: 2/3;
}

@media (min-width: 768px) {
  .galeria .imagen:nth-child(3) {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
.galeria .imagen:nth-child(6) {
  grid-row: 2/4;
}

@media (min-width: 768px) {
  .galeria .imagen:nth-child(6) {
    grid-column: 3/4;
    grid-row: 1/3;
  }
}
.footer {
  margin-top: 5rem;
  background-image: url(../img/header.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
.footer__copyright {
  background-color: #111111;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}
.footer__texto {
  margin: 0;
  color: #FFFFFF;
}
.footer__texto:nth-child(1) {
  display: none;
}
@media (min-width: 768px) {
  .footer__texto:nth-child(1) {
    display: unset;
  }
}